import React, { useState, useEffect } from "react";
import axios from "axios";

const App = () => {
  const [post, setPost] = useState([]);

  useEffect(() => {
    axios.get('https://omash-server.herokuapp.com/api/persons').then(response => {
      setPost(response.data)
    })
  }, [])

  console.log(post)

  const theStyle = {
    width: "18rem",
  };

  return (
    <div className="App bg-light">
      <h2>All Posts</h2>
      <div>
        <h2>{post.title}</h2><br />
        <p>{post.body}</p>
      </div>
      <div className="row gx-4 d-flex justify-content-center">
        {post.map((p) => (
          <div className="card p-3" key={p.id} style={theStyle}>
            <div className="card-body">
              <h5 className="card-title">card {p.id}</h5>
              <p className="card-text">{p.name}</p>
              <p className="card-text">{p.number}</p>
              <button type="button" class="btn btn-primary">
                read more
              </button>
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default App;
